import React from 'react';

import image404 from '@images/asset_9.svg';
import { ContainerWrapper, SectionHeader } from '@src/Styles';

export const headerText = `Something went wrong and we cannot find what you're looking for...`;
export const homepageUrl = 'https://marketprotocol.io/';
export const docsUrl = 'https://docs.marketprotocol.io/';
export const faqUrl = 'https://docs.marketprotocol.io/#faq-general';

class Page404 extends React.Component {
  render() {
    return (
      <div
        style={{
          padding: '70px 25px'
        }}
      >
        <ContainerWrapper>
          <div style={{ textAlign: 'center' }}>
            <SectionHeader color={'#000'}>{headerText}</SectionHeader>
            <br />
            <img alt="404" src={image404} />
          </div>

          <div style={{ padding: '40px 0px 20px 0px' }}>
            <SectionHeader color={'#000'}>Get back on track</SectionHeader>
            <ul>
              <li>
                Try heading back to our <a href={homepageUrl}>homepage</a>
              </li>
              <li>
                Check out our <a href={faqUrl}>FAQ</a>
              </li>
              <li>
                Read our <a href={docsUrl}>docs</a>
              </li>
            </ul>
          </div>
        </ContainerWrapper>
      </div>
    );
  }
}

export default Page404;



